import React, { useState, useEffect } from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Checkbox from '@mui/material/Checkbox';

const STORAGE_KEY = 'attendeeData'; // Define a storage key
const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

export default function CustomizedTables({ jsonData }) {
    const [attendeeData, setAttendeeData] = useState([]);

    useEffect(() => {
      // Load attendee data from local storage when the component mounts
      const storedData = localStorage.getItem(STORAGE_KEY);
      if (storedData) {
        setAttendeeData(JSON.parse(storedData));
      } else {
        // If no data in local storage, set it from the provided JSON data
        setAttendeeData(jsonData);
      }
    }, [jsonData]); // Make sure to re-run this effect when jsonData changes
  
    const handleCheckboxClick = (event, index) => {
      const updatedData = [...attendeeData];
      updatedData[index].isChecked = event.target.checked;
      setAttendeeData(updatedData);
  
      // Save updated attendee data to local storage
      localStorage.setItem(STORAGE_KEY, JSON.stringify(updatedData));
    };
  
    return (
      <TableContainer component={Paper} className='table-responsive'>
        <Table aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell></StyledTableCell>
              <StyledTableCell>Roll Number</StyledTableCell>
              <StyledTableCell align="right">Name</StyledTableCell>
              <StyledTableCell align="right">Year</StyledTableCell>
              <StyledTableCell align="right">Branch</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {attendeeData.map((data, index) => (
              <StyledTableRow key={data["Roll Number"]} selected={data.isChecked}>
                <StyledTableCell padding="checkbox">
                  <Checkbox
                    checked={data.isChecked}
                    onClick={(event) => handleCheckboxClick(event, index)}
                  />
                </StyledTableCell>
                <StyledTableCell component="th" scope="row">
                  {data["Roll Number"]}
                </StyledTableCell>
                <StyledTableCell align='right'>{data.Name}</StyledTableCell>
                <StyledTableCell align='right'>{data.Year}</StyledTableCell>
                <StyledTableCell align="right">{data.Branch}</StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    );
  }
